import { connect } from 'react-redux';
import App from './App';
import { fetchAllCapsules } from '../redux/actions/appActions';

const mapStateToProps = (state) => ({

    fetchAllCapsulesStart: state.fetchCapsules.fetchAllCapsulesStart,
    fetchAllCapsulesSuccess: state.fetchCapsules.fetchAllCapsulesSuccess,
    fetchAllCapsulesFailed: state.fetchCapsules.fetchAllCapsulesFailed,
    capsules: state.fetchCapsules.capsules,
    error: state.fetchCapsules.error
})

const mapDispatchToProps = (dispatch) => ({

    fetchAllCapsules: () => dispatch(fetchAllCapsules())
})

export default connect(mapStateToProps, mapDispatchToProps)(App);